var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"protocol"},[_c('nav',{staticClass:"header-bar"},[_c('span',{staticClass:"fa fa-chevron-left",attrs:{"onclick":"window.history.go(-1)"}}),_vm._v(" "),_c('span',[_vm._v("博客用户协议")]),_vm._v(" "),_c('span')]),_vm._v(" "),_c('div',{staticClass:"container"},[_c('h4',[_vm._v("《博客用户协议》")]),_vm._v(" "),_c('article',[_vm._v("\n        1. 特别提示\n      1.1\n      博客平台同意按照本协议的规定及其不时发布的操作规则向用户提供博客服务，为获得博客服务，博客服务使用人（以下称\"用户\"）需在认真阅读及独立思考的基础上认可、同意本协议的全部条款（特别是以加粗方式提示用户注意的条款）并按照页面上的提示完成全部的注册程序。用户在进行注册程序过程中点击\"同意\" 按钮（或实际使用博客服务）即表示用户完全接受本服务协议及《新浪网络服务使用协议》、《博客社区公约》、《博客商业行为规范办法》、《博客举报投诉操作细则》及博客平台公示的各项规则、规范。\n\n      1.2\n      用户注册成功后，博客平台将为用户基于使用博客服务的客观需要而在申请、注册博客服务时，按照注册要求提供的帐号开通博客服务，用户有权在博客平台为其开通并同意向其提供服务的基础上使用博客服务。同时，用户同意：\n\n      1.2.1 用户应妥善保管博客帐号及密码，未经博客平台同意，用户不得擅自买卖、转让、出租任何博客帐号或博客昵称。\n\n      1.2.2 用户使用博客服务过程中，须对自身使用博客服务的行为，对任何由用户通过博客服务发布、公开的信息，及对由此产生的任何后果承担全部责任。\n\n      1.2.3 用户提交、发布或显示的信息将对其他博客服务用户及第三方服务及网站可见(用户可通过设置功能自行控制、把握可查阅其信息的帐号类型)。\n\n      1.3\n      未经博客平台事先书面许可，用户不得自行授权任何第三方使用博客内容（博客内容即指用户在博客上已发布的信息，例如文字、图片、视频、音频等），包括但不限于自行授权任何第三方发表、复制、转载、更改、引用、链接、下载、同步或以其他方式使用部分或全部博客内容等。\n\n      1.4\n      用户在使用博客服务过程中应当严格遵守博客平台发布的Robots协议，链接地址包括但不限于：\n      http://weibo.com/robots.txt\n      http://weibo.cn/robots.txt\n      http://m.weibo.cn/robots.txt\n      未经微梦公司事先书面同意，任何用户不得以任何方式自行或委托任何第三方以违反上述规定的方式访问博客平台或收集任何博客内容。\n\n      1.5\n      用户同意并授权博客平台以博客平台名义就侵犯用户合法权益的行为（包括但不限于私自复制、使用、编辑、抄袭、在第三方平台上再次发布博客内容等行为）采取任何形式的法律行为，包括但不限于投诉、诉讼等必要的维权措施。\n\n      1.6\n      博客平台有权直接将本服务或本协议项下权利义务委托给博客平台的关联公司或其他第三方公司进行运营、管理及履行，博客平台无需就此向用户另行获取授权。博客平台将竭尽避免前述委托或变更给用户使用博客服务造成的不便，博客平台届时将尽量及时通过网站、平台、私信、邮件等方式进行通知。本协议中，关联公司是指控制某一方的、或被某一方所控制的、或与某一方共同受控制于同一实体的任何企业。控制是指直接或间接拥有该企业百分之五十（50％）以上的投票权或管理权。\n\n      1.7\n      为提高用户的博客服务使用感受和满意度，用户同意无偿授权博客平台以任何形式（商业或非商业）将基于用户的操作行为对用户数据进行调查研究和分析，从而进一步优化博客服务。\n      2. 服务内容\n\n      2.1\n      博客服务的具体内容由博客平台根据实际情况提供，包括但不限于授权用户通过其帐号，使用博客服务发布观点、评论、图片、视频、转发链接、长博客等，博客平台有权对其提供的服务或产品形态进行升级或其他调整，并将及时更新页面/告知用户。\n\n      2.2\n      博客平台提供的部分博客服务为收费网络服务，用户使用收费网络服务需要向博客平台支付一定的费用。对于收费的网络服务，博客平台会在用户使用之前给予用户明确的提示，只有用户根据提示确认其愿意支付相关费用，用户才能使用该等收费网络服务。如用户拒绝支付相关费用，则博客平台有权不向用户提供该等收费网络服务。\n\n      2.3\n      用户理解，博客平台仅提供与博客服务相关的技术服务等，除此之外与相关网络服务有关的设备（如个人电脑、手机、及其他与接入互联网或移动网有关的装置）及所需的费用（如为接入互联网而支付的电话费及上网费、为使用移动网而支付的手机费）均应由用户自行负担。\n      3. 服务变更、中断或终止\n\n      3.1\n      鉴于网络服务的特殊性（包括但不限于服务器的稳定性问题、恶意的网络攻击等行为的存在及其他博客平台无法控制的情形），用户同意博客平台有权随时中断或终止部分或全部的博客服务（包括收费网络服务），若发生该等中断或中止博客服务的情形，博客平台将尽可能及时通过网页公告、系统通知、私信、短信提醒或其他合理方式通知受到影响的用户。如因博客平台原因致使收费性博客服务中断或终止的，博客平台将该用户剩余虚拟货币退还用户的虚拟货币账户或向受影响的用户提供等值的替代性的收费网络服务。\n\n      3.2\n      用户理解，博客平台需要定期或不定期地对提供博客服务的平台（如互联网网站、应用程序等）或相关的设备进行检修或者维护，如因此类情况而造成服务在合理时间内的中断，博客平台无需为此承担任何责任，但博客平台应尽可能事先进行通告。\n\n      3.3\n      如发生下列任何一种情形，博客平台有权随时中断或终止向用户提供本协议项下的博客服务（包括收费服务和免费服务）而无需对用户或任何第三方承担任何责任，由此造成的损失由用户自行独立承担：\n\n      3.3.1 用户提供的个人资料不真实；\n\n      3.3.2 用户违反法律法规国家政策或本协议中规定的使用规则；\n\n      3.3.3 用户在使用收费服务时未按规定为其所使用的收费服务支付相关服务费用；\n\n      3.3.4 用户侵犯公民个人、社会组织、企事业单位合法权益的，包括但不限于侮辱、诽谤、谩骂公民个人，诋毁社会组织或企事业商誉或信誉等；\n\n      3.3.5 用户损害监管部门、国家机关及政府形象；\n\n      3.3.6 用户以任何方式损害博客平台及其关联公司的商誉或信誉等合法权益；\n\n      3.3.7 其他博客平台认为需要中断或终止向用户提供博客服务的。\n\n      3.4\n      如用户在申请开通博客服务后在任何连续90日内未实际使用，则博客平台有权选择采取以下任何一种方式进行处理：\n\n      3.4.1 回收用户昵称；\n\n      3.4.2 回收用户帐号，或停止为该用户提供博客服务。\n\n      3.5\n      用户选择将博客帐号与博客合作的第三方帐号进行绑定的，除用户自行解除绑定关系外，如发生下列任何一种情形，用户已绑定的第三方帐号也有可能被解除绑定，博客平台无需对用户或任何第三方承担任何责任：\n\n      3.5.1 用户违反法律法规国家政策、本协议的；\n\n      3.5.2 用户违反第三方用户协议或其相关规定的；\n\n      3.5.3 第三方账户所属的站方要求解除绑定的；\n\n      3.5.4 第三方账户所述平台或业务已关停的；\n\n      3.5.5 依据法律法规、政策规定、或主管部门要求的；\n\n      3.5.6 博客平台认为需要解除绑定的。\n      4. 使用规则\n\n      4.1\n      用户可自行编辑注册信息中的帐号名称、昵称、头像、简介等（以下简称“帐号信息”），但应遵守“七条底线”以及相关管理规定，不得含有违法和不良信息。\n      用户注册博客帐号，制作、发布、传播信息内容的，应当使用真实身份信息及个人资料，不得以虚假、冒用的居民身份信息、企业注册信息、组织机构代码信息进行注册；若用户的个人资料有任何变动，用户应及时更新。\n      未经相关权利人授权，用户不得以他人或其他组织机构名义注册博客帐号，亦不得使用引人误解的帐号信息注册博客帐号，包括但不限于让人误认为该帐号与某社会名人或机构组织存在关联关系的名称、头像或简介等。\n\n      4.2\n      新闻媒体和政务机关开设博客账号的，除根据本协议约定使用博客服务以外，还应遵守相关法律法规、组织规则及监管规定。\n\n      4.2.1 媒体账号，是指具有法定新闻资质的新闻单位在博客上开设的机构认证账号。媒体账号在使用博客服务时应当接受新闻主管部门和网信部门的统一管理。\n\n      4.2.2 政务账号，是指各级党政机关、民主党派、人民团体开设的机构认证账号。政务账号在使用博客服务时应当接受网信部门的统一管理，并根据自身的职责、章程和上级单位的管理要求进行管理。\n\n      4.3\n      用户知悉并同意，以下信息一经用户提交确认则无法更改，因内容瑕疵（包括但不限于错误，不完整、不满意等）所造成的损失及后果由用户独立承担：\n\n      4.3.1 博客帐号个性域名；\n\n      4.3.2 博客平台规定的确定后无法修改的帐号信息。\n\n      4.4\n      如用户违反前述约定，依据相关法律、法规及国家政策要求，博客平台有权随时采取不予注册、通知限期改正、注销登记用户帐号、中止或终止用户对博客服务的使用等措施，并有权向相关主管部门报告。\n\n      4.5\n      博客平台将建立健全用户信息安全管理制度、落实技术安全防控措施。博客平台将对用户使用博客服务过程中涉及的用户隐私内容加以保护。\n      对于用户在博客中发表或存储的博客内容，用户应当自行不定期进行备份。用户在使用博客服务期间及终止使用博客服务后，博客平台均无义务向用户返还或提供任何数据或博客内容。\n\n      4.6\n      为维护博客平台的稳定运营，确保用户体验质量，未经博客平台事先书面许可，任何人不得擅自在博客平台上实施自动化行为或发布垃圾信息。\n\n      4.6.1 自动化行为，是指以用户自行或委托他人采用自动化手段或明显异于常人的、远高于正常用户频率地发布博客、评论、私信、头条文章或作出关注、点赞、抓取数据等的行为。\n\n      4.6.2 垃圾信息，是指未经博客平台同意，擅自使用博客帐号（包括通过作弊手段批量注册的博客帐号、普通博客帐号等）在博客、评论、私信、帐号信息、头条文章、群聊内容中发布的营销信息、无意义信息或卖粉信息。\n      用户知悉并同意，博客平台有权根据技术规则通过检测验证等方式判断用户帐号行为是否构成自动化行为、用户帐号所发布的信息是否为垃圾信息，并采取相关措施予以处理。所有处理措施所依据的数据及信息（包括但不限于帐号操作记录、博客内容、转评赞等）均以博客平台后台记录为准。\n\n      4.7\n      由于博客服务的存在前提是用户在申请开通博客服务的过程中所提供的帐号，则用户不应将其帐号、密码转让或出借予他人使用。如用户发现其帐号或博客服务遭他人非法使用，应立即通知博客平台。因黑客行为或用户的保管疏忽等非博客平台原因导致帐号、密码及博客服务遭他人非法使用，博客平台有权拒绝承担任何责任。\n\n      4.8\n      用户同意博客平台在提供博客服务过程中以各种方式投放各种商业性广告或其他任何类型的商业信息（包括但不限于在博客平台网站的任何页面上投放广告），并且，用户同意接受博客平台通过电子邮件、私信或其他方式向用户发送商品促销或其他相关商业信息。\n\n      4.9\n      用户知悉、理解并同意授权博客平台及其关联公司可在全球范围内、完全免费、可转授权地使用用户通过博客发布的内容，前述内容包括但不限于文字、图片、视频等。具体来说，可能会包括：\n\n      4.9.1 将前述内容通过博客自身或其他第三方技术、网络等在博客平台选择的网络平台、应用程序或产品中，以有线或无线网，通过免费或收费的方式在不同终端（包括但不限于电脑、手机、互联网电视、机顶盒及其他上网设备等）以不同形式（包括但不限于点播、直播、下载等）进行网络传播或电信增值服务等；\n\n      4.9.2 将前述内容复制、翻译、编入博客平台当前已知或以后开发的作品、媒体或技术中，用于博客相关用途开发或推广宣传等；\n\n      4.9.3 将前述内容授权给电台、电视台、网络媒体、运营商平台等与博客平台有合作的媒体或运营商播放、传播，用于博客相关推广宣传等；\n\n      4.9.4 其他博客平台及其关联公司出于善意或另行取得用户授权的使用行为。\n      用户对博客平台及其关联公司的前述授权并不改变用户发布内容的所有权及知识产权归属，也并不影响用户行使其对发布内容的合法权利；\n      博客平台将尽最大的商业努力合理使用用户的授权内容，但并不代表博客平台及其关联公司承诺一定会使用。\n\n      4.10\n      用户在使用博客服务的过程中应文明发言，并依法尊重其它用户的人格权与身份权等人身权利，共同建立和谐、文明、礼貌的网络社交环境。\n\n      4.11\n      用户在使用博客服务过程中，必须遵循以下原则：\n\n      4.11.1 不得违反中华人民共和国法律法规及相关国际条约或规则；\n\n      4.11.2 不得违反与网络服务、博客服务有关的网络协议、规定、程序及行业规则；\n\n      4.11.3 不得违反法律法规、社会主义制度、国家利益、公民合法权益、公共秩序、社会道德风尚和信息真实性等“七条底线”要求；\n\n      4.11.4 不得进行任何可能对互联网或移动网正常运转造成不利影响的行为；\n\n      4.11.5 不得上传、展示或传播任何不实虚假、冒充性的、骚扰性的、中伤性的、攻击性的、辱骂性的、恐吓性的、种族歧视性的、诽谤诋毁、泄露隐私、成人情色、恶意抄袭的或其他任何非法的信息资料；\n\n      4.11.6 不得以任何方式侵犯他人依法享有的专利权、著作权、商标权等知识产权，或姓名权、名称权、名誉权、荣誉权、肖像权、隐私权等人身权益，或其他任何合法权益；\n\n      4.11.7 不得以任何方式侵犯公民个人、社会组织、企事业单位合法权益的，包括但不限于侮辱、诽谤、谩骂公民个人，诋毁社会组织或企事业商誉或信誉等；\n\n      4.11.8 不得以任何方式损害各级国家机关及政府形象；\n\n      4.11.9 不得以任何方式损害博客平台及其关联公司的商誉或信誉等合法权益；\n\n      4.11.10不得从事其他任何影响博客平台正常运营、破坏博客平台经营模式或其他有害博客平台生态的行为。\n\n      4.11.11不得为其他任何非法目的而使用博客服务。\n\n      4.12\n      针对某些特定的博客服务的使用规则及说明，博客平台将通过各种方式（包括但不限于网页公告、系统通知、私信、短信提醒等）作出的任何声明、通知、警示等内容视为本协议的一部分，用户如使用该等博客服务，视为用户同意该等声明、通知、警示的内容。\n\n      4.13\n      博客平台有权对用户使用博客服务的行为及信息进行审查、监督及处理，包括但不限于用户信息（帐号信息、个人信息等）、发布内容（位置、文字、图片、音频、视频、商标、专利、出版物等）、用户行为（构建关系、@信息、评论、私信、参与话题、参与活动、营销信息发布、举报投诉等）等范畴。\n\n      4.14\n      如用户在使用博客服务的过程中发现其它用户上传违法侵权等内容，用户可直接点击\"举报\"按键进行举报，相关人员会尽快核实并进行处理；如涉及姓名权、名称权、名誉权、荣誉权、肖像权、隐私权等人身权益纠纷的处理，用户可依据相关法律法规并参照站方有关公告所公示的方式进行处理；如用户认为上述方法无法解决遇到的问题、或用户觉得有必要向司法行政机关寻求帮助的，用户应尽快向相关机关反馈，博客平台将依法配合司法机关的调查取证工作。\n      5. 知识产权\n\n      5.1\n      博客平台是博客平台和博客产品的所有权及知识产权权利人。\n\n      5.2\n      上述博客产品指的是博客平台、或其关联公司、或其授权主体等通过博客平台为用户提供的包括但不限于信息发布分享、关系链拓展、便捷辅助工具、平台应用程序、公众开放平台等功能、软件、服务等。\n\n      5.3\n      博客平台是博客平台及博客产品中所有信息内容的所有权及知识产权权利人。前述信息内容包括但不限于程序代码、界面设计、版面框架、数据资料、帐号、文字、图片、图形、图表、音频、视频等，除按照法律法规规定应由相关权利人享有权利的内容以外。\n\n      5.4\n      用户在使用博客平台的过程中，可能会使用到由第三方针对博客服务开发的在博客平台运行的功能、软件或服务，用户除遵守本协议相关规定以外，还应遵守第三方相关规定，并尊重第三方权利人对其功能、软件、服务及其所包含内容的相关权利。\n\n      5.5\n      鉴于以上，用户理解并同意：\n\n      5.5.1 未经博客平台及相关权利人同意，用户不得对上述功能、软件、服务进行反向工程 （reverse engineer）、反向编译（decompile）或反汇编（disassemble）等；同时，不得将上述内容或资料在任何媒体直接或间接发布、播放、出于播放或发布目的而改写或再发行，或者用于其他任何目的；\n\n      5.5.2 在尽商业上的合理努力的前提下，博客平台并不就上述功能、软件、服务及其所包含内容的延误、不准确、错误、遗漏或由此产生的任何损害，以任何形式向用户或任何第三方承担任何责任；\n\n      5.5.3 博客平台并不对上述任何由第三方提供的功能、软件、服务或内容进行任何保证性的、或连带性的承诺或担保，由此产生的任何纠纷、争议或损害，由用户与第三方自行解决，博客平台不承担任何责任；\n\n      5.5.4 为更好地维护博客生态，博客平台保留在任何时间内以任何方式处置上述由博客平台享受所有权及知识产权的产品或内容，包括但不限于修订、屏蔽、删除或其他任何法律法规允许的处置方式。\n      6. 隐私保护\n\n      6.1\n      本协议所指的“隐私”，是指《电信和互联网用户个人信息保护规定》第4条关于个人信息、《最高人民法院关于审理利用信息网络侵害人身权益民事纠纷案件适用法律若干问题的规定》第12条关于个人隐私、以及未来不时制定或修订的法律法规中明确规定的隐私应包括的内容。\n\n      6.2\n      保护用户隐私和其他个人信息是博客平台的一项基本政策，博客平台保证不会将单个用户的注册资料及用户在使用博客服务时存储在博客平台的非公开内容用于任何非法的用途，且保证将单个用户的注册资料进行商业上的利用时应事先获得用户的同意，但下列情况除外：\n\n      6.2.1 事先获得用户的明确授权；\n\n      6.2.2 为维护社会公共利益；\n\n      6.2.3 学校、科研机构等基于公共利益为学术研究或统计的目的，经用户书面同意，且公开方式不足以识别具体用户身份；\n\n      6.2.4 用户自行在网络上公开的信息或其他已合法公开的个人信息；\n\n      6.2.5 以合法渠道获取的个人信息；\n\n      6.2.6 用户侵害博客平台及其关联公司合法权益，为维护前述合法权益且在必要范围内；\n\n      6.2.7 根据相关政府主管部门的要求；\n\n      6.2.8 根据相关法律法规或政策的要求；\n\n      6.2.9 博客平台认为有必要的其他情况。\n\n      6.3\n      为提升博客服务的质量，博客平台可能会与第三方合作共同向用户提供相关的博客服务，此类合作可能需要包括但不限于博客用户数据与第三方用户数据的互通。在此情况下，用户知晓并同意，如用户自行授权同意或该第三方同意承担与博客平台同等的保护用户隐私的责任，则博客平台有权将用户的信息（包括但不限于注册资料等）提供给该第三方，并与第三方约定用户数据仅为双方合作的博客服务之目的使用；并且，博客平台将对该等第三方使用用户数据的行为进行监督和管理，尽一切合理努力保护用户个人信息的安全性。\n      7. 免责声明\n\n      7.1\n      用户在使用博客服务的过程中应遵守国家法律法规及政策规定，因其使用博客服务而产生的行为后果由用户自行承担。\n\n      7.2\n      通过博客服务发布的任何信息，及通过博客服务传递的任何观点不代表博客平台之立场，博客平台亦不对其完整性、真实性、准确性或可靠性负责。用户对于可能会接触到的非法的、非道德的、错误的或存在其他失宜之处的信息，及被错误归类或是带有欺骗性的发布内容，应自行做出判断。在任何情况下，对于任何信息，包括但不仅限于其发生的任何错误或遗漏；或是由于使用通过博客服务发布、私信、传达、其他方式所释出的或在别处传播的信息，而造成的任何损失或伤害，应由相关行为主体承担全部责任。\n\n      7.3\n      鉴于外部链接指向的网页内容非博客平台实际控制，因此博客平台无法保证为向用户提供便利而设置的外部链接的准确性和完整性。\n\n      7.4\n      对于因不可抗力或博客平台不能控制的原因造成的博客服务中断或其它缺陷，博客平台不承担任何责任，但将尽力减少因此而给用户造成的损失和影响。\n\n      7.5\n      用户同意，对于博客平台向用户提供的下列产品或者服务的质量缺陷本身及其引发的任何损失，博客平台无需承担任何责任：\n\n      7.5.1 博客平台向用户免费提供的博客服务；\n\n      7.5.2 博客平台向用户赠送的任何产品或者服务；\n\n      7.5.3 博客平台向收费博客服务用户附赠的各种产品或者服务。\n\n      7.6\n      用户知悉并同意，博客平台可能会与第三方合作向用户提供产品（包括但不限于游戏、第三方应用等）并由第三方向用户提供该产品的升级、维护、客服等后续工作，由该等第三方对该产品的质量问题或其本身的原因导致的一切纠纷或用户损失承担责任，用户在此同意将向该第三方主张与此有关的一切权利和损失。\n\n      7.7\n      博客平台上提